import React from "react"
import Seo from "../components/SEO"
import { GatsbyImage, getImage, StaticImage } from "gatsby-plugin-image"
import BannerModule from "../components/BannerModule/BannerModule"
import { ArtWork } from "../models/artist"
import Layout from "../components/Layout"
import { ArtworkStyles } from "../styles/ArtworkStyles"
import { classNames } from "../constants/globals"
import { CloudinaryContext, Image } from "cloudinary-react"


const ProductTemplate = ({ pageContext: { artist, artwork } }) => {
    let _artwork = ArtWork.fromContentful(artwork, artist)
    const {
        title, minimumPrice, introduction, description, gallery, picture,
    } = _artwork
    const pictureImage = getImage(picture.fullsize)
    return (<>
        <Seo title={title} />
        <Layout>
            <BannerModule
                title={title}
                price={minimumPrice}
                subTitle={introduction}
                enquire={true}
            >
                <ArtworkStyles>
                    <center className={"artwork-container"}>
                        <CloudinaryContext cloudName="dsfxktb5y"> {/* TODO cloudName="dsfxktb5y" */}
                            <Image publicId="sample" width="0.5" />
                        </CloudinaryContext>
                        <StaticImage
                            className={classNames("horizontal", "artwork_img_frame")}
                            src="../../static/frames/frame-horizontal.png"
                            alt={title}
                        />
                        <GatsbyImage
                            className="artwork_img_pic horizontal"
                            image={pictureImage}
                            width={300}
                            alt={title} />
                    </center>
                </ArtworkStyles>
            </BannerModule>
        </Layout>
        {/* TODO
            <Features
                title=" Featured Products from The Famous Cat."
                introduction=" Vivamus quam mauris, pulvinar vel mauris id, interdum semper neque. Proin malesuada
                    libero eget tellus scelerisque, id egestas tortor egestas."
                    />
                    {*/}
    </>)
}

export default ProductTemplate
